.nums-list {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  &__item {
    display: block;
    height: 20px;
    width: 20px;
    text-align: center;
    line-height: 20px;
    &:first-child {
      align-self: center;
      margin: 5px 5px 0 5px;
    }
    &:last-child {
      align-self: center;
      margin: 5px 5px 0 5px;
    }
    &:nth-child(3) {
      align-self: flex-end;
    }
    &:nth-child(2) {
      position: relative;
      left: 16px;
      margin-top: 5px;
    }
    &:nth-child(3) {
      position: relative;
      margin-bottom: 5px;
      right: 16px;
    }
  }
}
